
import Player from '@/utils/game/Player';
import { Options, Vue } from 'vue-class-component';

import Logo from '../components/Logo.vue'
import PlayersTable from '../components/PlayersTable.vue'
import Countdown from '../components/Countdown.vue'

import genWorker from '../utils/GenWorker'

@Options({
  components: {
    Logo, PlayersTable, Countdown
  },

  data: () => ({
    countdownStarted: false,
    autoStartTimerId: null,
    autoStartCountdown: 10
  }),

  methods: {
    resetGame() {
      this.$store.dispatch('resetGameState');
    },

    startCountdown() {
      this.resetGame();
      this.$store.dispatch('setGenLevelState', false);

      genWorker.postMessage({
        type: 'genLevel',
        payload: {
          level: 1,
          settings: {
            useFakeLetters: this.$store.state.useFakeLetters,
            useHiddenLetters: this.$store.state.useHiddenLetters
          }
        }
      });

      this.countdownStarted = true;
    },

    autoStartCountdownDecrease() {

      if (this.autoStartCountdown > 0) {
        this.autoStartCountdown --;
      }
      else if (!this.countdownStarted) {
        this.startCountdown();
        clearInterval(this.autoStartTimerId);
      }

    },

    startGame() {
      this.$router.push('/game');
    },

    exit() {
      this.resetGame();
      this.$router.push('/');
    }
  },

  beforeMount () {
    if (!this.$store.state.gameStarted) {
      this.$router.push('/');
      return;
    }

    this.autoStartTimerId = setInterval(this.autoStartCountdownDecrease, 1000);
  },

  beforeUnmount () {
    if (this.autoStartTimerId) {
      clearInterval(this.autoStartTimerId);
    }
  }
})
export default class GameOver extends Vue {}
